<style>
  td {
    color: black;
    text-shadow: 1px 1px lightgrey;
  }
  tr.monday {
    background-color: lightgoldenrodyellow;
  }
  tr.tuesday {
    background-color: lightpink;
  }
  tr.wednesday {
    background-color: lightseagreen;
  }
  tr.thursday {
    background-color: lightsalmon;
  }
  tr.friday {
    background-color: lightsteelblue;
  }
  tr.saturday {
    background-color: wheat;
  }
  tr.sunday {
    background-color: coral;
  }
</style>

<div class="container">
  <div class="row">
    <div class="col">
      <table class="table table-sm table-hover">
        <thead class="thead-dark">
          <tr>
            <th>Date</th>
            <th>Activity</th>
            <th>Comments</th>
            <th>Hours</th>
            <th>
              <div class="form-group form-check">
                <input
                  type="checkbox"
                  class="form-check-input"
                  id="displayWeekends"
                  bind:checked={displayWeekends}
                  on:change={refreshTable} />
                <label class="form-check-label" for="displayWeekends">
                  Display Weekends
                </label>
              </div>
            </th>
          </tr>
        </thead>
        <tbody>
          {#each displayEntries as timeEntry}
            <tr class={timeEntry.jornada.diaSemana.toLowerCase()}>
              <td>{timeEntry.spent_on} {timeEntry.jornada.diaSemana}</td>
              <td>{timeEntry.activity}</td>
              <td>{timeEntry.comments}</td>
              <td>{timeEntry.hours}</td>
              <td>
                <button
                  type="button"
                  class="btn btn-sm {timeEntry.id < 0 ? 'btn-success' : 'btn-primary'}"
                  data-toggle="modal"
                  data-target="#timeEntriesForm"
                  on:click={(e) => handleModalOpen(e, timeEntry)}>
                  <i class="fas fa-calendar-plus" />
                </button>
              </td>
            </tr>
          {/each}
        </tbody>
      </table>
    </div>
  </div>
  <!-- Modal -->
  <div
    class="modal fade"
    id="timeEntriesForm"
    tabindex={-1}
    role="dialog"
    aria-labelledby="timeEntriesFormTitle"
    aria-hidden="true">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header {modalWarning ? 'bg-warning' : ''}">
          <h5 class="modal-title" id="timeEntriesFormTitle">
            Register Time Entries for {modalData.turno.diaSemana}
            {modalData.turno.fecha}
          </h5>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
            on:click={handleModalClose}>
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <table>
            <caption>
              <button
                type="button"
                class="btn btn-sm btn-info"
                on:click={(e) => handleAddEmptyEntryInModal(e)}>
                <i class="fas fa-calendar-plus" /> Add empty entry
              </button>
            </caption>
            <thead>
              <tr>
                <th>Issue</th>
                <th>Activity</th>
                <th>Comments</th>
                <th>Hours</th>
                <th />
              </tr>
            </thead>
            <tbody>
              {#each modalData.entries as entry, index}
                <tr>
                  <td>
                    <select
                      bind:value={entry.issue}
                      class="form-control"
                      name="Issue">
                      <option value="" selected>--Select an issue--</option>
                      {#each $issues as issue (issue.id)}
                        <option value={issue.id}>
                          {issue.id}
                          {issue.project.name}
                        </option>
                      {/each}
                    </select>
                  </td>
                  <td>
                    <select
                      bind:value={entry.activity}
                      class="form-control"
                      name="Activity">
                      <option value="" selected>--Select an activity--</option>
                      {#each $activities as activity (activity.id)}
                        <option value={activity.id}>{activity.name}</option>
                      {/each}
                    </select>
                  </td>
                  <td>
                    <input
                      type="text"
                      bind:value={entry.comments}
                      class="form-control"
                      placeholder="Comments" />
                  </td>
                  <td>
                    <input
                      type="number"
                      min="0.0"
                      max="24.0"
                      step="0.5"
                      bind:value={entry.hours}
                      class="form-control"
                      placeholder="Hours" />
                  </td>
                  <td>
                    <button
                      type="button"
                      class="btn btn-sm btn-danger"
                      on:click={(e) => removeModalEntry(e, index)}>
                      <i class="fas fa-trash" />
                    </button>
                  </td>
                </tr>
              {/each}
            </tbody>
          </table>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-dismiss="modal"
            on:click={handleModalClose}>
            Close
          </button>
          <button
            type="button"
            class="btn btn-danger"
            data-dismiss="modal"
            on:click={async () => await handleModalSave()}>
            Submit Time Entries
          </button>
        </div>
      </div>
    </div>
  </div>
</div>
